/* App.css */

body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f9f9f9;
  color: #333;
}

.app-container {
  padding: 2rem;
  max-width: 100%;
  box-sizing: border-box;
}

.app-header {
  font-size: 1.8rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 1.5rem;
  color: #222;
}

.msr-title {
  color: #d32f2f; /* red tone for highlight */
}

.search-bar {
  display: block;
  margin: 0 auto 1.5rem auto;
  padding: 0.6rem 1rem;
  font-size: 1rem;
  width: 90%;
  max-width: 400px;
  border: 1px solid #ccc;
  border-radius: 8px;
}

.table-container {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.06);
}

.leaderboard-table {
  width: max-content;
  min-width: 100%;
  border-collapse: collapse;
}

.leaderboard-table th,
.leaderboard-table td {
  padding: 10px 12px;
  border-bottom: 1px solid #eee;
  text-align: center;
  white-space: nowrap;
}

.leaderboard-table th {
  background-color: #f0f0f0;
  position: sticky;
  top: 0;
  z-index: 1;
  font-size: 0.95rem;
}

.highlight-msr {
  font-weight: bold;
  color: #1976d2;
}

@media (max-width: 768px) {
  .app-container {
    padding: 1rem;
  }

  .app-header {
    font-size: 1.4rem;
  }

  .leaderboard-table {
    font-size: 0.85rem;
  }
}













